<template>
  <div>
    <a-list item-layout="vertical" size="large" :data-source="listData">
      <template #renderItem="{ item }">
        <a-list-item key="item.title">
          <template v-if="!loading" #actions>
            <span v-for="{ type, text } in actions" :key="type">
              <component :is="type" style="margin-right: 8px"></component>
              {{ text }}
            </span>
            <span>阅读原文</span>
          </template>
          <a-skeleton :paragraph="{ rows: 4, width: '100%' }" :loading="loading" active avatar>
            <a-list-item-meta :description="item.description">
              <template #title>
                <a @click="goPage(item.title)">{{ item.title }}</a>
                <span class="article-tags">{{item.tags}}</span>
              </template>
              <!-- <template #avatar><a-avatar :src="item.avatar" /></template> -->
            </a-list-item-meta>
            {{ item.content }}
          </a-skeleton>
        </a-list-item>
      </template>
    </a-list>
    <a-pagination size="small" :total="50" show-size-changer show-quick-jumper />
  </div>
</template>
<script>
import { defineComponent, ref, reactive } from 'vue';
import {useRouter} from 'vue-router'
const listData = [];
for (let i = 0; i < 13; i++) {
  listData.push({
    href: 'https://www.antdv.com/',
    title: `ant design vue part ${i}`,
    tags: 'css',
    avatar: 'https://joeschmoe.io/api/v1/random',
    description:
      'Ant Design, a design language for background applications, is refined by Ant UED Team.Ant Design, a design language for background applications, is refined by Ant UED Team.',
    content:
      'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.',
  });
}
export default defineComponent({
  name: 'TechBlog',
  setup() {
    let loading = ref(false);
    const data = reactive({
      content: '',
      text: '22',
      test: 1,
      pageList: [
        {title: '博客'}
      ]
    })
    const actions = [
      { type: 'star-outlined', text: '156' },
      { type: 'like-outlined', text: '156' },
      { type: 'message-outlined', text: '2' },
    ];
    const router = useRouter()
    const goPage = (id) => {
      router.push('/article', {id})
    }
    return {
      data,
      loading,
      listData,
      actions,
      goPage
    };
  }
});
</script>
<style lang="scss">
.ant-list-item{
  transition: all 200ms;
  background: white;
  cursor: pointer;
  margin-bottom: 20px;
  border-radius: 6px;
  box-shadow: 2px 2px 8px rgba(156, 156, 156, 0.3);
  &:hover {
    box-shadow: 2px 2px 8px rgb(156, 156, 156);
  }
}
.ant-pagination {
  text-align: right;
}
</style>
<style>
#components-layout-demo-fixed .logo {
  width: 120px;
  height: 31px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px 24px 16px 0;
  float: left;
}
.site-layout .site-layout-background {
  background: #fff;
}

[data-theme='dark'] .site-layout .site-layout-background {
  background: #141414;
}
</style>